import React from 'react';
import './card.style.css'
const Card = props => (

    <div className='card-container'>
        <img alt='player' src = {`https://robohash.org/${props.counter}?set=set2&size=180x180`}></img>
       <h1>{props.player}</h1> 
       <p>{props.position} - &nbsp;
       {props.team}</p>
    </div>
)
export default Card